@include tablet {
  .products {
    padding-bottom: 36.004px;

    &__category-top {
      display: block;
      padding-top: 3.255vw;
      border-top: 0.052vw solid #eae6e6;
    }

    &__category-title {
      font-weight: 700;
      font-size: 4.948vw;
      line-height: 5.729vw;
      text-align: center;
      margin-bottom: 3.255vw;
      span {
        font-weight: 500;
        font-size: 2.344vw;
        line-height: 2.734vw;
        color: rgba(0, 0, 0, 0.47);
      }
    }

    &__show-menu {
      padding: 5.208vw 3vw;
      transform: translateX(-100%);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--light-color);
      z-index: 100;
    }

    &__btns {
      display: flex;
      justify-content: space-between;
    }

    &__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 600;
      font-size: 2.604vw;
      line-height: 4.427vw;
      border: 1px solid var(--black-color);
      border-radius: 1.302vw;
      width: 44.792vw;
      height: 7.813vw;
      transition: all 0.3s ease-in-out;
      svg {
        fill: var(--black-color);
        width: 3vw;
        height: 3vw;
        margin-left: 2vw;
        transition: all 0.3s ease-in-out;
      }

      &:focus {
        color: var(--light-color);
        background-color: var(--red-color);
      }

      &:focus svg {
        fill: var(--light-color);
      }
    }

    &__show-btn--close {
      position: absolute;
      top: 3vw;
      right: 3vw;
      z-index: 3;
      svg {
        width: 3.906vw;
        height: 3.906vw;
      }
    }

    &__show-title {
      text-align: center;
      font-family: var(--font-family-Book-antiqua);
      font-weight: 400;
      font-size: 6.25vw;
      line-height: 7.552vw;
      text-transform: uppercase;
      margin-bottom: 5.208vw;
    }

    &__inner {
      padding: 10.417vw 0;
      border-bottom: 0;
      border-top: 0;
    }

    &__header {
      height: 100%;
      overflow: auto;
      padding-bottom: 10vw;
    }

    &__items {
      width: 44.271vw;
    }

    &__boubles {
      bottom: 53.76px;
      left: 107.52px;
      width: 159.206px;
      height: 194.004px;
    }

    &__pattern {
      top: 130.56px;
      right: 38.4px;
      width: 102.405px;
      height: 60.004px;
    }

    &__content {
      grid-column-gap: 2.604vw;
      grid-row-gap: 6.51vw;
    }
  }

  .product {
    &__item {
      &:not(:last-child) {
        margin-bottom: 19.999px;
      }
    }

    &__item-kind {
      margin-bottom: 5.998px;
    }

    &__item-title {
      font-size: 4.948vw;
      line-height: 5.729vw;
      span {
        margin: 0 auto 0 2vw;
        font-size: 2.344vw;
        line-height: 2.734vw;
      }

      svg {
        width: 3vw;
        height: 3vw;
      }
    }

    &__item-link {
      &:not(:last-child) {
        margin-bottom: 1.953vw;
      }
      a {
        font-size: 3.125vw;
        line-height: 3.646vw;
      }
    }

    &__card {
      width: 100%;
      height: 100%;
    }

    &__card-image {
      border-radius: 2.302vw;
    }

    &__card-title {
      font-size: 2.604vw;
      line-height: 4.427vw;
      bottom: -2vw;
      width: 35.938vw;
      height: 5.208vw;
      border-radius: 5.469vw;
    }
  }

  .filter-menu--active {
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(0);
  }
}
